@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap');


:root{


	--yellow-theme-background-color :  #181818;  /*orginal #1d1d1d */
	--yellow-theme-main-color : #7d3cff; /*orginal #ffdd40 */
	--yellow-theme-sub-text-color : #ffffff; /*orginal #ffffff */
	--yellow-theme-nav-background-color : #181818; /* orginal #181818 */

}

*{
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	scroll-behavior: smooth;
}

html {

	font-size: 62.5%;
	height: 100%;
}

body {

	background: var(--yellow-theme-background-color);
	height: 100%;
}

#root{

	height: 100%;
}

